/*================================
 loadingComputerChooseImpulse Div
 ===============================*/
/* Design and Animation when player waits for computer choose */
.loadingComputerChooseImpulse {
  width: 230px;
  height: 230px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.568);
  margin: 0 auto;
  display: flex;
  /* align horizontal */
  justify-content: center;
  /* align vertical */
  align-items: center;
  flex-flow: column;
  -webkit-animation: loadingAnimation 3s linear;
          animation: loadingAnimation 3s linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  /* Second Div inside Parent Div. This is grey/white circle */ }
  .loadingComputerChooseImpulse .loadingComputerChooseImpulse2 {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin: auto;
    background-color: grey;
    -webkit-animation: loadingAnimation2 3s linear;
            animation: loadingAnimation2 3s linear;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; }

/* First animation keyframes*/
@-webkit-keyframes loadingAnimation {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  50% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    visibility: hidden; } }
@keyframes loadingAnimation {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  50% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    visibility: hidden; } }

/* Second animation keyframes*/
@-webkit-keyframes loadingAnimation2 {
  0%,
  20%,
  40%,
  75% {
    opacity: 1;
    background-color: black; }
  10%,
  30%,
  50% {
    background-color: #fff;
    opacity: 0; }
  100% {
    background-color: #fff;
    opacity: 1;
    visibility: hidden; } }
@keyframes loadingAnimation2 {
  0%,
  20%,
  40%,
  75% {
    opacity: 1;
    background-color: black; }
  10%,
  30%,
  50% {
    background-color: #fff;
    opacity: 0; }
  100% {
    background-color: #fff;
    opacity: 1;
    visibility: hidden; } }

